$color1: #041c32;
$color2: #04293a;
$color3: #064663;
$color4: #ecb365;
$color5: #ffffff;
$color6: #eeeeee;

//break point
$break-point_xl: 1200px;
$break-point_lg: 1024px;
$break-point-md: 768px;
$break-point-sp: 540px;
